//ËØ•Êñá‰ª∂‰∏ìÈó®Áî®‰∫éÂàõÂª∫Êï¥‰∏™Â∫îÁî®ÁöÑË∑ØÁî±Âô®
import VueRouter from "vue-router";
//ÂºïÂÖ•ÁªÑ‰ª∂
import Home from '../pages/Home'
import About from '../pages/About'
import News from '../pages/News'
import Message from '../pages/Message'
import Detail from '../pages/Detail'


//ÂàõÂª∫‰∏Ä‰∏™Ë∑ØÁî±Âô®
const router = new VueRouter({
    routes:[
        {
            name:'guanyv',
            path:'/about',
            component:About,
            meta:{title:'ÂÖ≥‰∫é'}
        },
        {
            name:'zhuye',
            path:'/home',
            component:Home,
            meta:{title:'‰∏ªÈ°µ'},
            children:[
                {
                    name:'xinwen',
                    path:'news',
                    component:News,
                    meta:{title:'Êñ∞Èóª'},
                    // üî¥Áã¨‰∫´ÂÆàÂç´ÔºåÁâπÂÆöË∑ØÁî±ÂàáÊç¢‰πãÂêéË¢´Ë∞ÉÁî®
                    beforeEnter(to,from,next){
                        console.log('Áã¨‰∫´Ë∑ØÁî±ÂÆàÂç´',to,from)
                        if(localStorage.getItem('school') === 'atguigu'){
                            next()
                        }else{
                            alert('ÊöÇÊó†ÊùÉÈôêÊü•Áúã')
                        }
                    }
                },
                {
                    name:'xiaoxi',
                    path:'message',
                    component:Message,
                    meta:{title:'Ê∂àÊÅØ'},
                    children:[
                        {
                            name:'xiangqing',
                            path:'detail',
                            component:Detail,
                            meta:{title:'ËØ¶ÊÉÖ'},
                            props($route){
                                return {
                                    id:$route.query.id,
                                    title:$route.query.title,
                                }
                            }
                        }
                    ]
                }
            ]
        }
    ]
})

//ÂÖ®Â±ÄÂêéÁΩÆË∑ØÁî±ÂÆàÂç´‚Äî‚Äî‚Äî‚ÄîÂàùÂßãÂåñÁöÑÊó∂ÂÄôË¢´Ë∞ÉÁî®„ÄÅÊØèÊ¨°Ë∑ØÁî±ÂàáÊç¢‰πãÂêéË¢´Ë∞ÉÁî®
router.afterEach((to,from)=>{
	console.log('ÂêéÁΩÆË∑ØÁî±ÂÆàÂç´',to,from)
	document.title = to.meta.title || 'Á°ÖË∞∑Á≥ªÁªü'
})

//ÂØºÂá∫Ë∑ØÁî±Âô®
export default router